@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@font-face {
  font-family: 'Jameel';
  src: url("/Saqaya_Welfare/static/media/Jameel Noori Nastaleeq Regular.a1d0cd1f.ttf") format("truetype");
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Jameel';
}
body {
  font-family: 'Jameel'!important;
}
body{
  overflow-x: hidden;
}

body {
  background-color: #ADD9E6;
}
.link{
color: #000;
}
.link:hover{
color: #000;
}
.welfare_name{
margin-right: 30px;
}
.main_div{
    width: 100vw;
    height: 100vh;
    background-color: #ADD8E6;
}
/* Header Part */
.main_div .header_div{
    width: 100%;
    height: 50px;
    background:  #90DAF2;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 0 10px #969090;
    margin-bottom: 10px;
}

.header_div div:nth-child(1){
    margin-left: 20px;
}
.header_div div:nth-child(3){
    margin-right: 30px;
}

/* Check Box Part */
.input_box{
    width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

/* .input_box .search_box{
    width: 1200px;
} */
.search_box{
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.search_box input{
    width: 80%;
    height: 100%;
    border: none;
    font-size: 16px;
    background: #E1E1E1;
    border-radius: 10px;
    text-align: right;
    padding: 10px 20px;
    color: #000;
}

input:focus{
    outline: none;
}

.button {
    width: 150px;
    height: 45px;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    background-color: #76d1f0;
    cursor: pointer;
}
/* .expense{
    background-color: #10b4f0;
} */
.input_box .button{
    font-weight: bold;
}
.category_type{
    width: 100%;
    max-height: 50px;
    height: 100%;
    margin-top: 10px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.button:hover{
    text-shadow: 1px 1px 2px rgb(158, 158, 158), 0 0 1em rgb(175, 175, 175), 0 0 0.2em rgb(216, 216, 216);
}
.masjid_list{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin-top: 10px;
}
.filter_Masjid_name{
    width: 90%;
    margin-top: 10px;
}
.data{
    width: 100%;
    color: black;
    background: red;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #E1E1E1;
    border-radius: 10px;
}
.data p{
    margin-right: 20px;
    font-size: 20px;
}
.data h1{
    width: 250px;
    text-align: right;
}
/* Resposive CSS  */

@media only screen and (max-width: 768px){
    .category_type{
        margin-top: 0px;
        flex-wrap: wrap;
        font-size: 12px;
    }
    .input_box{
        flex-wrap: wrap;
    }
    .search_box{
        width: 100%;
        margin: 5px 10px;
        flex-wrap: wrap;
    }
    .search_box  > input, .search_box > div{
        width: 100%;
    }
    .input_box .button{
        width: 100%;
        margin: 5px 10px;
    }
    .header_div{
        display: flex;
        justify-content: center;
        background: red;
    }
    .Header_date, .category_name{
        display: none;
    }
    .header_div > .welfare_name{
        margin-right: 0px;
        width: 100%;
        text-align: center;
    }
    .category_type .button{
        margin-top: 5px;
    }
    /* .category_name{
        margin-left: 10px;
    } */
    .header_div div:nth-child(3){
        margin-right: 10px;
    }
    .account_ledger{
       display: flex;
       flex-direction: column-reverse;
       justify-content:right;
      } 
    .account_ledger_details div, .account_text div{
        text-align: center;
    }
    .account_ledger .accoutDetail{
        margin-top: 10px;
      }
      .masjid_list{
          margin-top: 100px;
          justify-content: flex-start;
      }
      .data{
          flex-wrap: wrap;
          align-items: flex-end;
          flex-direction: column-reverse;
          padding:10px;
      }
}

@media only screen and (max-width: 1024px){
    .input_box .search_box{
        width: 800px;
    }
}
.load{
    background-image: radial-gradient(#ADD9E6,#ADD9E6, white) !important;
    /* background-color: #ADD9E6; */
}
.load{
    height: 100vh; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;   
}

.load img{
    height : 20%;
     width : 30%;
}
.main_div {
  width: 100vw;
  height: 100vh;
  background-color: #add8e6;
}

.account,
.account_header,
.account_ledger_details {
  width: 94%;
  background: #e1e1e1;
  margin: 0 auto;
  margin-top: 10px;
  text-align: right;
  padding: 10px 20px;
  border-radius: 20px;
  cursor: pointer;
  color: black;
}

.account_ledger,
.account_text,
.account_ledger_details {
  display: flex;
  flex-direction: row;
  flex: 1 1;
  justify-content: space-around;
  /* display: grid;
  grid-template-columns: repeat(3, 1fr); */
  font-size: 20px;
}
.account_header span {
  font-size: 24px;
  font-weight: bold;
}

/* Account Info */
.account .account_ledger{
  display: flex;
  justify-content: space-around;
}
.account_ledger .button{
  height: 30px;
}

/* Masjid Expense */
.account_ledger_details div, .account_text div{
  width: 200px;
  text-align: center;
}
.masjid_detail{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
}
.masjid_detail .button{
    flex: 0.1 1;
}
.masjid_header_info{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    align-items: center;
    flex: 0.8 1;
}
.masjid_header_info h1{
    margin-right: 40px;
}
.masjid_header_info p{
    margin-bottom: 0px;
    font-size: 18px;
}
.expense_table{
    padding: 10px;
}
TableBody TableRow TableCell{
    background-color: red !important;
}
@media only screen and (max-width: 768px){
    .masjid_detail{
        flex-wrap: wrap;
        flex-direction: column;
        margin-top: 20px;
    }
    .masjid_header_info{
        flex-wrap: wrap;
        justify-content: flex-start;
        flex-direction: column;
        margin-top: 10px;
    }
    .masjid_header_info h1{
        margin-right: 0px;
    }
}
